/* TOOLBAR */

.toolbar {
	// IE7
	.ie7 & {
		.personal-bar {
			width: 100%;
			.personal-nav {
				float: right;
				width: auto;
				.personal-nav-item {
					display: inline;
					float: left;

					text-align: right;
				}
			}
		}
		.bar {
			width: 100%;
		}
	}
	// IE8
	.ie8 & {
		.personal-bar {
			height: 32px;
		}
		.bar {
			width: 100%;
		}
	}
	// IE9
	.ie9 & {
		// styles here
	}
	// IE10
	.ie10 & {
		// styles here
	}
}
